<template>
<div class='hello'>
  <form>
    <div class="form-group">
      <label for="exampleInputPassword1">displayName</label>
      <input type="text" class="form-control" v-model='displayName' v-bind:disabled='true'>
    </div>
    <img v-bind:src="photoURL">
    <div class="form-group">
      <label for="exampleInputPassword1">EMAIL</label>
      <input type="text" class="form-control" v-model='email' v-bind:disabled='true'>
    </div>
    <div class="form-group">
      <label for="exampleInputPassword1">UID</label>
      <input type="text" class="form-control" v-model='uid' v-bind:disabled='true'>
    </div>

    <button type="button" class="btn btn-danger" v-on:click='logout'>登出</button>
  </form>
</div>
</template>

<script>
import firebase from 'firebase'

export default {
  name: 'Hello',
  data: function () {
    return {
      loading: true,
      user: {}
    }
  },
  methods: {
    logout: function () {
      firebase.auth().signOut().then(() => {
        this.$router.replace('login')
      })
    }
  },
  beforeCreate: function () {
    firebase.auth().onAuthStateChanged((user) => {
      this.user = user
      if (user) {
        this.$user.displayName = user.displayName
        this.$user.photoURL = user.photoURL
        this.$user.email = user.email
        this.$user.uid = user.uid
      }
      this.loading = false
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



