import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/Login'
import SignUp from '@/components/SignUp'

import Hello from '@/components/Hello'
import Groups from '@/components/Groups'

import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [{
    path: '*',
    redirect: '/login'
  }, {
    path: '/',
    redirect: '/login'
  }, {
    path: '/login',
    name: 'Login',
    component: Login
  }, {
    path: '/sign-up',
    name: 'SignUp',
    component: SignUp
  }, {
    path: '/hello',
    name: 'Hello',
    component: Hello,
    meta: {
      requiresAuth: true
    }
  }, {
    path: '/groups',
    name: 'Groups',
    component: Groups,
    meta: {
      requiresAuth: true
    }
  }]
})

router.beforeEach((to, from, next) => {
  // 這裡會一直檢查登入情形
  let currentUser = firebase.auth().currentUser
  let requiresAuth = to.matched.some(record => record.meta.requiresAuth)

  if (currentUser) {
    Vue.prototype.$user.displayName = currentUser.displayName
    Vue.prototype.$user.photoURL = currentUser.photoURL
    Vue.prototype.$user.email = currentUser.email
    Vue.prototype.$user.uid = currentUser.uid
  }

  if (requiresAuth && !currentUser) next('login')
  else if (!requiresAuth && currentUser) next('groups')
  else next()
})

export default router



