<template>
<div class='groups'>
  <table class="table">
    <thead class="thead-dark">
      <th> <button type="button" class="btn btn-primary" v-on:click='create'>新團</button></th>
      <th>團名</th>
      <th>開團時間</th>
      <th>團主</th>
    </thead>
    <tbody class='table table-striped'>
      <tr v-for='item in groups'>
        <td><button type="button" class='btn btn-danger' v-on:click='deleteItem(item)' v-if='$user.uid == item.createdBy'>刪除</button></td>
        <td> <input class="form-control" :value="item.name" @input="updateItem(item, $event.target.value)"></td>

        <td>{{item.createdAt}}</td>
        <td>{{item.createdBy}}</td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
import moment from 'moment'
import {
  firebaseDb
} from '../firebaseDb'
// var storageRef = firebase.storage().ref();

export default {
  name: 'Groups',
  firebase: {
    groups: {
      source: firebaseDb.ref('/groups/')
    }
    // anArray: firebase.database().ref('/groups/')
  },
  data() {
    return {
      loading: true
    }
  },
  methods: {
    create() {
      this.$firebaseRefs.groups.push({
        name: '[未命名的飲料團]',
        createdBy: this.$user.uid,
        createdAt: moment().format('YYYY-MM-DD HH:mm')
      })
    },
    deleteItem: function(item) {
      this.$firebaseRefs.groups.child(item['.key']).remove()
    },
    updateItem: function(item, newName) {
      this.$firebaseRefs.groups.child(item['.key']).child('name').set(newName)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



